* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
@font-face {
  font-family: 'Averta CY';
  src: url('AvertaCY.woff') format('woff');
}

body {
background: #fff;
}
img {
  display: block;
  margin: 0 auto;
}

body,
button {
  font-family: "Averta CY", sans-serif;
}

.app {
  width: auto;
  margin: 0 auto;

}
.terms logo {
    margin-right: 2rem;
}
.terms img {
    width: 160px;
    height: 60px;
}
.terms h1 {
 margin: 50px;
 font-family: "Averta CY";
 font-size: 32px;
 font-weight: bold;
text-align: center;
}
.terms h3 {
 margin: 40px;
 font-family: "Averta CY";
 font-size: 32px;
text-align: left;
}
.terms p {
 font-size: 18px;
 margin: 50px;
}
.terms h4 {
font-size: 18px;
 margin: 20px;
}
.terms h2 {
 font-size: 26px;
  margin: 40px;
  transition: ease 0.5s;
}
.terms a{
 color: #000;
}
.terms a:hover{
 color: ##383535;
 text-decoration: underline;
}
select option:checked {
  background-color: red;
  color: white;
}
::-webkit-scrollbar{
    width: 10px;
}
::-webkit-scrollbar-track{
    background: transparent;
}
::-webkit-scrollbar-thumb{
    width: 10px;
    background: #000000;
    border-radius: 10px;
    transition : ease 0.2s;
}
::-webkit-scrollbar-thumb:hover{
    background: #383535;
}
@media screen and (max-width: 480px) {
  .terms h1 {
    font-size: 25px;
    margin: 20px;
  }
  
  .terms h3 {
    font-size: 20px;
    margin: 20px;
  }
  
  .terms p {
    font-size: 14px;
    margin: 5px;
  }
  
  .terms h4 {
    font-size: 14px;
    margin: 5px;
  }
  
  .terms h2 {
    font-size: 18px;
    margin: 5px ;
  }
  
  .terms img {
    width: 80px;
    height: 30px;
  }
}
@media screen and (max-width: 400px) {
  .terms h1 {
    font-size: 20px;
    margin: 20px;
  }
  
  .terms h3 {
    font-size: 15px;
    margin: 20px;
  }
  
  .terms p {
    font-size: 12px;
    margin: 5px;
  }
  
  .terms h4 {
    font-size: 12px;
    margin: 5px;
  }
  
  .terms h2 {
    font-size: 12px;
    margin: 10px ;
  }
  
  .terms img {
    width: 80px;
    height: 30px;
  }
}    
